%flexbox {
  display: flex;
  justify-content: center;
  align-items: center;
}

%sectionHome {
  .titulo_box {
    @extend %flexbox;
    flex-direction: column;

    margin-bottom: 1rem;
  }

  .titulo_box .titulo {
    margin-bottom: 0.5rem;

    font-weight: bold;
    font-size: $f-grande;

    color: $c-azul;
  }

  .titulo_box .subtitulo {
    font-size: $f-pequeno;
    color: $c-cinza;
  }
}

%separador {
  width: 5rem;
  height: 0.2rem;

  margin: 1rem 0 1rem 0;

  background: $c-cinzaClaro;
  border-radius: 0.2rem;
}

%borda {
  border: 1px solid $c-cinzaClaro;
}

%passagem {
  @extend %borda;
  padding: 1.5rem;

  @extend %flexbox;
  flex-direction: column;

  text-align: center;

  .img_passagem {
    width: 10rem;

    margin-bottom: 2rem;
    @extend %borda;
  }

  .titulo {
    margin-bottom: 1rem;

    font-weight: bold;
    color: $c-preto;
  }

  .subtitulo {
    font-size: $f-pequeno;
  }

  .valor {
    margin: 1rem 0 2rem 0;

    font-weight: bold;
    font-size: 1.3rem;

    color: $c-preto;
  }

  .btn_comprar {
    width: 100%;

    @extend %flexbox;
    padding: 1rem;

    text-transform: uppercase;
    font-weight: bold;

    background: $c-azul;
    color: white;

    transition: 0.2s ease-in-out;
    &:hover {
      background: rgb(0, 134, 192);
    }
  }
}

%slider {
  @extend %flexbox;
  flex-direction: column;

  @extend %borda;
  padding: 1.5rem;

  .titulo,
  .subtitulo {
    margin-bottom: 1rem;
  }

  .titulo {
    font-weight: bold;
    font-size: $f-grande;

    color: $c-preto;

    span {
      font-size: 1rem;
    }
  }

  .btn_visualizar {
    width: 10rem;

    padding: 1rem;
    margin-top: 1rem;

    font-weight: bold;
    text-transform: uppercase;
    color: $c-cinza;

    background: $c-cinzaClaro;

    a {
      color: $c-cinza;
    }
  }
}

%paginaTitulo {
  padding: 8rem 5vw 0 5vw;

  .titulo_box {
    background: $c-branco;
    padding: 3rem 5vw 3rem 5vw;
    margin: 0 -5vw 0 -5vw;
    margin-bottom: 3rem;
  }

  .titulo {
    font-size: $f-grande;
  }
}

%separadorVertical {
  display: none;

  width: 1px;
  height: 10rem;

  background: linear-gradient(to bottom, transparent, $c-cinza, transparent);
  justify-self: center;
}

%tabela {
  overflow-x: auto;

  #tabela {
    @extend %borda;

    thead {
      background: $c-cinza;
    }

    th {
      font-size: 1rem;
      color: $c-branco;
      font-weight: bold;

      border-right: 1px solid #7e93a8;
      &:nth-last-child(1) {
        border-right: none;
      }
    }

    td {
      @extend %borda;

      &:nth-last-child(1) {
        border-right: none;
      }
    }

    th,
    td {
      padding: 1rem;
    }
  }

  .valoresDoVT .observacao {
    margin-top: 1rem;

    font-style: italic;
    font-size: $f-pequeno;
  }
}

%form {
  .form {
    .anexarAquivo {
      margin-top: 1rem;

      .texto {
        font-weight: bold;
        margin-bottom: 0.5rem;
      }
    }

    .btn_enviar-box {
      width: 100%;
      display: flex;
      justify-content: flex-end;

      button {
        width: 10rem;
        padding: 1rem;

        background: $c-azul;
        color: $c-branco;

        text-transform: uppercase;
        font-weight: bold;
      }
    }

    textarea {
      font-family: "Lato", sans-serif;
    }

    .campos_principais {
      display: grid;
    }
  }

  @media #{$m-pequeno} {
    .form {
      .campos_principais {
        grid-template-columns: 1fr 1fr;

        .campo_input-box:nth-child(odd) {
          margin-right: 0.5rem;
        }
        .campo_input-box:nth-child(even) {
          margin-left: 0.5rem;
        }
      }
    }
  }
}
